<template lang="pug">
    .note(@click="openNote")
        .images(v-if="isImages")
            .image(v-for="item in data.tools.images", :data-size="setSize", :style="{'background-image': 'url('+item.dataURL+')'}")

        .container
            .content-text
                h2.title {{data.title}}
                p.description {{data.description}}
                
                .list(v-if="isList")
                    .item(v-for="item in data.tools.list")
                        span.text {{item.text}}

                .checklist(v-if="isChecklist")
                    .item(v-for="item in data.tools.checklist")
                        span.text {{item.text}}

            .footer
                .date-edit 8 horas atrs

</template>

<script>
    export default {
        name: 'we-note',
        data () {
            return {
                isImages: false,
                isChecklist: false,
                isList: false,
                msg: 'Welcome to Your Vue.js App'
            }
        },
        computed:{
            setSize(){
                if (this.data.tools.images.length === 1) {
                    return 1
                }else if(this.data.tools.images.length === 2){
                    return 2
                }else if(this.data.tools.images.length >= 3){
                    return 3
                }
            }
        },
        mounted(){
            this.checkImages();
            this.checkChecklist();
            this.checkList();
        },
        methods: {
            checkList(){
                if (this.data.tools.list) {
                    if (this.data.tools.list.length >= 1) {
                        this.isList = true;
                    }else{
                        this.isList = false;
                    }
                }
            },
            checkChecklist(){
                if (this.data.tools.checklist) {
                    if (this.data.tools.checklist.length >= 1) {
                        this.isChecklist = true;
                    }else{
                        this.isChecklist = false;
                    }
                }
            },
            checkImages(){
                if (this.data.tools.images) {
                    if (this.data.tools.images.length >= 1) {
                        this.isImages = true;
                    }else{
                        this.isImages = false;
                    }
                }
            },
            openNote(){
                let checkDrop = this.$el.getAttribute('data-drag');
                
                if (!checkDrop) {
                    this.$router.push({
                        name: 'note',
                        params: {
                            idNote: this.data.id
                        }
                    })
                }
            }
        },
        props: ['data']
    }
</script>
<style lang="stylus" scope src="@/assets/styles/notes/Note.styl"></style>


