<template lang="pug">
    button.button-share-user(@click="openShare")
</template>

<script>
    export default {
        name: 'w-userThumb',
        props: ['data'],
        data () {
            return {
                msg: 'Welcome to Your Vue.js App'
            }
        },
        methods:{
            openShare(){
                document.querySelector('.share-user').classList.add('show');
            }
        }
    }
</script>
<style lang="stylus" scope src="@/assets/styles/common/ButtonShareUser.styl"></style>


