<template lang="pug">
    .share-user
        .backgroundOverlay(@click="closeShare")
        
        //- .users-default(v-if="usersDefaultShare.length")
        //-     we-userThumb(v-for="user in usersDefaultShare", :data="user")

        //- .users(v-if="tempSelectUser.length")
        //-     we-userThumb(v-for="user in tempSelectUser", :data="user")
        
        .wrapper-share-user
            //- 
            input.input-text(placeholder="Buscar")

            .list-search
                .user(v-for="user in this.users", @click.stop="selectUser(user, $event.target)")
                    .photo(:style="{'background-image':'url('+user.infos.photo+')'}")
                    .infos
                        span.name {{user.infos.name}}
                        span.count-task 2 task

        .footer-share
            button.button-add(@click="saveUsers") Adicionar mais 2 usuarios
            button.button-cancel Cancelar

</template>

<script>
    import WeUserThumb from '@/components/common/We-UserThumb.vue'
    import Users from '@/users.js'

    export default {
        name: 'w-shareUser',
        props: {
            data: Array
        },
        components:{
            'we-userThumb': WeUserThumb
        },
        data () {
            return {
                users: Users.users,
                userSelect: [],
                tempSelectUser: [],
                usersDefaultShare: [],
                msg: 'Welcome to Your Vue.js App'
            }
        },
        watch: {
            tempSelectUser(data){
                this.$emit('selectUsers', data);
            }
        },
        created(){
            this.getCurrentUsersShare();
        },
        methods: {
            stop(){},
            saveUsers(){
                for (var i = 0; i < this.userSelect.length; i++) {
                    this.tempSelectUser.push(this.userSelect[i]);
                }
                this.closeShare();
            },
            getCurrentUsersShare(){
                if (this.data) {
                    for (var i = 0; i < this.data.length; i++) {
                        if (!this.data[i].master) {
                            this.usersDefaultShare.push(this.data[i])
                        }
                    }
                }
            },
            selectUser(user, el){
                el.classList.add('active');
                this.userSelect.push(user);
            },
            closeShare(){
                document.querySelector('.share-user').classList.remove('show');
            }
        },
        beforeDestroy(){
            // this.tempSelectUser = [];
        }
    }
</script>
<style lang="stylus" scope src="@/assets/styles/common/ShareUser.styl"></style>


