<template lang="pug">
    .notes
        .overlay(@click="close")
        .content
            .header-notes
                h2.title Notas
                button.button-filter

            .created-note(ref="createdNote")
                span.place(@click="showCreated") Anote aqui
                
                .group
                    .wrap-creates
                        we-editable.title(
                            ref = 'title',
                            :content="contentNote.title",
                            @update="contentNote.title = $event", 
                            placeholder="Titulo"
                        )

                        we-editable.descrition(
                            ref = 'description',
                            :content="contentNote.description",
                            @update="contentNote.description = $event", 
                            placeholder="Descrio"
                        )
                        
                        .inserts(v-for="(child, index) in inserts")
                            component(:is="child.name", :key="index", @return="dataInsert")
                        
                        .files-uploads(v-if="files.length")
                            .header-uploads
                                h3.title Arquivos anexados

                            .list-files
                                .file(v-for="file in files", @click="remove(file)")
                                    img.image(:src="file.dataURL")

                                    .details
                                        span.filename {{file.name}}
                                        span.size {{file.size}}
                                    
                        .buttons
                            button.button-list(@click="editorNote('list')") Lista
                            button.button-checklist(@click="editorNote('checklist')") Lista
                            button.button-image Lista
                            

                    .footer-created
                        .hashtag
                            .header-hashtag
                                button.button-public
                                input(@click="showHashtags", type="text", placeholder="#hashtag")
                            
                            .public-options
                                .option
                                    .icon
                                    span.text Publico
                                .option
                                    .icon
                                    span.text Privado

                            .list-hashtags
                                .hashtag-item(v-for="item in 4")
                                    .content-text
                                        span.title #comercial
                                        span.count 25 notas

                        .group-save
                            button.button-save(@click="saveNote") Publicar
                            button.button-cancel(@click="closeCreated") Cancelar
                            .users-notes
                                we-userThumb.master(:data="masteruser")
                                we-shareUser(@selectUsers="getUsersShare")

            .list-notes
                we-note(v-for="(note, index) in notes", :key="index", :data="note")

            button.open(@click="openNotes") Expandir
</template>

<script>
    import Vue from 'vue'
    import eventHub from '@/EventHub'
    import WeNote from '@/components/notes/We-Note.vue';
    import Packery from 'packery'
    import Muuri from 'muuri'
    import Notes from '@/components/notes/notes.js'
    import MasterUser from '@/master-user.js'
    import Draggabilly from 'draggabilly'
    import WeEditable from '@/components/common/We-Editable.vue'
    import WeShareUser from '@/components/common/We-ShareUser.vue'
    import WeUserThumb from '@/components/common/We-UserThumb.vue'

    import Dropzone from 'dropzone'
    import WeChecklist from '@/components/tools-created/We-CheckList.vue'
    import WeList from '@/components/tools-created/We-List.vue'
    import WeUpload from '@/components/tools-created/We-Upload.vue'

    import Files from '@/components/tools-created/files.js'

    export default {
        name: 'we-notes',
        components:{
            'we-editable': WeEditable,
            'we-note': WeNote,
            'we-shareUser': WeShareUser,
            'we-checklist': WeChecklist,
            'we-list': WeList,
            'we-upload': WeUpload,
            'we-userThumb': WeUserThumb
        },
        data () {
            return {
                grid: null,
                inserts: [],
                files: [],
                usersShare: [],
                myDropzone: null,
                contentNote: {
                    title: null,
                    description: null,
                    images: [],
                    list: [],
                    checklist: []
                },
                masteruser: MasterUser,
                notes: Notes.notes,
                msg: 'Welcome to Your Vue.js App'
            }
        },
        created(){
            let element = document.querySelector('body');

            element.addEventListener('click', (e) => {
                if (e.target.offsetParent) {
                    if (e.target.offsetParent.className != 'hashtag') {
                        this.closeHashtags()
                    }
                }
            });
        },
        mounted(){
            this.drag();
            this.upload();
            window.dispatchEvent(new Event('resize'));
        },
        watch: {
            files(data){
                this.contentNote.images = [];

                for (var i = 0; i < data.length; i++) {
                    this.contentNote.images.push(data[i])
                }
            }
        },
        methods: {
            getUsersShare(data){
                this.usersShare = data;
            },
            dataInsert(type, data){
                this.contentNote[type] = data;
            },
            upload(){
                this.myDropzone = new Dropzone(".button-image", {
                    url: 'https://appdividend.com/',
                    previewsContainer: false,
                    accept: (file, done) => {
                        this.files = this.myDropzone.files;
                    }
                });
            },
            saveNote(){
                let lastBoardId = this.notes[this.notes.length-1].id;

                let tempNote = {
                    id: lastBoardId,
                    date: '8 de maro',
                    title: this.contentNote.title,
                    description: this.contentNote.description,
                    users: [
                        {
                            id: 1,
                            master: true,
                            infos: {
                                photo: 'https://randomuser.me/api/portraits/women/12.jpg',
                                name: 'vital'
                            }
                        }
                    ],
                    tools: {
                        images: this.contentNote.images,
                        checklist: this.contentNote.checklist,
                        list: this.contentNote.list
                    }
                }

                for (var i = 0; i < this.usersShare.length; i++) {
                    tempNote.users.push(this.usersShare[i])
                }
                
                this.notes.push(tempNote);

                setTimeout(() => { 
                    this.grid.destroy();
                    this.drag();
                    this.clearCreated();
                }, 1);
            },
            remove(file){
                this.myDropzone.removeFile(file);
                this.files = this.myDropzone.files;
            },
            showHashtags(){
                this.$refs.createdNote.classList.add('show-hashtag');
            },
            closeHashtags(){
                this.$refs.createdNote.classList.remove('show-hashtag');
            },
            showCreated(){
                this.$refs.createdNote.classList.add('show-created');
                this.openNotes();
            },
            clearCreated(){
                let temp = {
                    title: null,
                    description: null,
                    images: null,
                    list: null,
                    checklist: null
                };

                this.contentNote = temp;
                this.inserts = [];
                this.files = [];
                this.myDropzone.removeAllFiles();
                this.$refs.title.$el.innerHTML = '';
                this.$refs.description.$el.innerHTML = '';
            },
            closeCreated(){
                this.$refs.createdNote.classList.remove('show-created');
                this.closeHashtags();
                this.clearCreated();
            },
            close(){
                document.querySelector('.main-wrapper').removeAttribute('data-show');
                document.querySelector('.main-wrapper').removeAttribute('data-share');
                this.updateGrid();
                this.closeCreated();
            },
            openNotes(){
                document.querySelector('.main-wrapper').setAttribute('data-show', 'notes');
                this.updateGrid();
            },
            updateGrid(){
                setTimeout(() => {
                    window.dispatchEvent(new Event('resize'));
                    this.grid.layout();
                }, 100);
            },
            editorNote(tollType){
                let temp = {
                    name: 'we-'+tollType
                }

                this.inserts.push(temp);
            },
            drag(index){
                this.grid = new Muuri('.list-notes', {
                    dragEnabled: true,
                    layoutDuration: 200,
                    dragSortInterval: 2,
                    layout: {
                        height: 800,
                        rounding: true
                    }
                });

                this.grid.on('dragMove', (item, event) => {
                    item._element.setAttribute('data-drag', true);
                });

                this.grid.on('dragStart', (item, event) => {
                    let notes = document.querySelectorAll('.notes .note');
                    for (var i = 0; i < notes.length; i++) {
                        notes[i].classList.add('box');
                    }

                    item._element.classList.remove('box')
                });

                this.grid.on('dragEnd', (item, event) => {
                    let notes = document.querySelectorAll('.notes .note');
                    for (var i = 0; i < notes.length; i++) {
                        notes[i].classList.remove('box');
                    }

                    setTimeout(() => {
                        item._element.removeAttribute('data-drag') 
                    }, 10);
                });
            }
        }
    }
</script>
<style lang="stylus" src="@/assets/styles/notes/Notes.styl"></style>


