<template lang="pug">
    .upload
        .file-upload Upload

        ul
            li(v-for="file in files") {{file.name}}

</template>

<script>
    import Vue from 'vue'
    import WeEditable from '@/components/common/We-Editable.vue'
    import Dropzone from 'dropzone'
    import Files from '@/components/tools-created/files.js'

    export default {
        name: 'we-upload',
        components:{
            'we-editable': WeEditable
        },
        data () {
            return {
                files: Files.files,
                myDropzone: null
            }
        },
        created(){
            
        },
        mounted(){
            this.upload();
        },
        watch: {
            
        },
        methods: {
            upload(){
                this.myDropzone = new Dropzone(".file-upload", {
                    url: 'https://appdividend.com/',
                    accept: (file, done) => {
                        this.files.push(file);
                    }
                });
            }
        }
    }
</script>
<style lang="stylus" src="@/assets/styles/tools-created/Upload.styl"></style>


