<template lang="pug">
	.wrapper-created-task
		.overlay(@click="close")
		button.button-trash
		.group
			.users-task
				we-userThumb.master(:data="masteruser")
				we-buttonShareUser
					
			.created
				we-shareUser(@selectUsers="getUsersShare")
				
				.content-created
					we-editable.title(
						:content="contentTask.title",
						@update="contentTask.title = $event", 
						placeholder="O que  pra fazer?"
					)
					we-editable.descrition(
						:content="contentTask.description",
						@update="contentTask.description = $event", 
						placeholder="Descrio.."
					)

					.inserts(v-for="(child, index) in inserts")
						component(:is="child.name", :key="index", @return="dataInsert")

					.files-uploads(v-if="files.length")
						.header-uploads
							h3.title Arquivos anexados

						.list-files
							.file(v-for="file in files", @click="remove(file)")
								img.image(:src="file.dataURL")

								.details
									span.filename {{file.name}}
									span.size {{file.size}}
									
					.buttons
						button.button-list(@click="editorTask('list')") Lista
						button.button-checklist(@click="editorTask('checklist')") Lista
						button.button-image Lista
				
				.footer-created
					.deadlines
						button.button.button-today(@click="deadline(1, $event.target)").active Hoje
						button.button.button-tomorrow(@click="deadline(2, $event.target)") Amanh
						datepicker.button.button-date(placeholder="Definir data", :language="ptBR", @opened="openDate")

					button.button-save(:class="activeButtons ? 'active' : ''", @click="saveTask") Salvar em minhas tarefas
					button.button-cancel(:class="activeButtons ? 'active' : ''") Cancelar

</template>

<script>
	import MasterUser from '@/master-user.js'
	import WeEditable from '@/components/common/We-Editable.vue'
	import WeShareUser from '@/components/common/We-ShareUser.vue'
	import WeUserThumb from '@/components/common/We-UserThumb.vue'
	import Datepicker from 'vuejs-datepicker';
	import {ptBR} from 'vuejs-datepicker/dist/locale'

	import Dropzone from 'dropzone'
	import WeChecklist from '@/components/tools-created/We-CheckList.vue'
	import WeList from '@/components/tools-created/We-List.vue'
	import WeUpload from '@/components/tools-created/We-Upload.vue'
	import Boards from '@/components/boards/boards.js'
	import WeButtonShareUser from '@/components/common/We-ButtonShareUser.vue'

	import Files from '@/components/tools-created/files.js'

	export default {
		name: 'w-created-task',
		components:{
			'we-buttonShareUser': WeButtonShareUser,
			'datepicker': Datepicker,
			'we-editable': WeEditable,
			'we-shareUser': WeShareUser,
			'we-userThumb': WeUserThumb,
			'we-checklist': WeChecklist,
			'we-list': WeList,
			'we-upload': WeUpload
		},
		data () {
			return {
				inserts: [],
				files: [],
				ptBR: ptBR,
				currentBoard: {
					id: null, 
					title: null
				},
				boards: Boards.boards,
				currentTaskCreated: null,
				usersShare: [],
				myDropzone: null,
				activeButtons: false,
				contentTask: {
					title: null,
					description: null,
					images: null,
					list: null,
					checklist: null
				},
				masteruser: MasterUser,
				msg: 'Welcome to Your Vue.js App'
			}
		},
		watch:{
			'contentTask.title'(old, current){
				this.checkInputs(old);
			},
			'contentTask.description'(old, current){
				this.checkInputs(old);
			}
		},
		mounted(){
			this.upload();
			this.getBoard(this.$route.params.id);
		},
		methods:{
			getUsersShare(data){
				this.usersShare = data;
			},
			getBoard(id){
				let tempBoardId = id;

				for (var i = 0; i < this.boards.length; i++) {
					if (this.boards[i].id == tempBoardId) {
						this.currentBoard.id = tempBoardId;
					}
				}
			},
			saveTask(){
				let tempCurrentBoard = null;
				let tempIdNewTask = null;

				for (var i = 0; i < this.boards.length; i++) {
					if (this.boards[i].id == this.currentBoard.id) {
						tempCurrentBoard = this.boards[i];
					}
				}

				if (tempCurrentBoard.cols[0].tasks.length) {
					let lastTaskId = tempCurrentBoard.cols[0].tasks[tempCurrentBoard.cols[0].tasks.length-1].id;
					tempIdNewTask = lastTaskId+1;
				}else{
					tempIdNewTask = 1;
				}
				
				let tempTask = {
					id: tempIdNewTask,
					users: [
						{
							id: 1,
							master: true,
							infos: {
								photo: 'https://randomuser.me/api/portraits/women/12.jpg',
								name: 'vital'
							}
						}
					],
					content: {
						title: this.contentTask.title,
						description: this.contentTask.description,
						date: 'Atrasado 2 dias',
						list: 0,
						comments: 0,
						anexos: 0
					},
					comments:[]
				};

				for (var i = 0; i < this.usersShare.length; i++) {
					tempTask.users.push(this.usersShare[i])
				}
				
				tempCurrentBoard.cols[0].tasks.push(tempTask);
				this.close();
			},
			openDate(){
				let buttons = document.querySelectorAll('.deadlines button')
				for (var i = 0; i < buttons.length; i++) {
					buttons[i].classList.remove('active')
				}

				document.querySelector('.button-date').classList.add('active');
			},
			dataInsert(type, data){
				this.contentTask[type] = data;
			},
			editorTask(tollType){
				let temp = {
					name: 'we-'+tollType
				}

				this.inserts.push(temp);
			},
			upload(){
				this.myDropzone = new Dropzone(".button-image", {
					url: 'https://appdividend.com/',
					previewsContainer: false,
					accept: (file, done) => {
						this.files = this.myDropzone.files;
					}
				});
			},
			remove(file){
				this.myDropzone.removeFile(file);
				this.files = this.myDropzone.files;
			},
			checkInputs(old){
				if (old != '') {
					this.activeButtons = true;
				}else{
					this.activeButtons = false;
				}
			},
			close(){
				this.$router.go(-1)
			},
			deadline(type, el){
				let buttons = document.querySelectorAll('.deadlines .button')
				for (var i = 0; i < buttons.length; i++) {
					buttons[i].classList.remove('active')
				}

				el.classList.add('active');
			}
		}
	}
</script>
<style lang="stylus" scope src="@/assets/styles/created-task/CreatedTask.styl"></style>


