<template lang="pug">
    .wrapper-checklist
        .header-list
            we-editable(
                class="editable-title",
                ref="title",
                :content="titleBlock",
                @update="titleBlock = $event", 
                placeholder="Titulo da checklist"
            )

        ul.checklist(v-if="items")
            li.item(v-for="(item, index) in items")
                input(type="checkbox", :name="index")
                .icon-check
                span.text {{item.text}}
                //- we-shareUser(currentPage="checklists")
        
        we-editable(
            class="editable-item",
            ref="editable",
            @keyup.enter.native="onCreatedItem",
            :content="contentChecklist.text",
            @update="contentChecklist.text = $event", 
            placeholder="Digitar novo item"
        )
        
</template>

<script>
    import Vue from 'vue'
    import WeEditable from '@/components/common/We-Editable.vue'
    import WeShareUser from '@/components/common/We-ShareUser.vue'
    import WeUserThumb from '@/components/common/We-UserThumb.vue'

    export default {
        name: 'we-checklist',
        components:{
            'we-editable': WeEditable,
            'we-shareUser': WeShareUser,
            'we-userThumb': WeUserThumb
        },
        data () {
            return {
                items: [],
                titleBlock: null,
                contentChecklist: {
                    text: ''
                }
            }
        },
        created(){
        },
        mounted(){
            this.$refs.editable.$el.focus();
        },
        watch: {
            items(data){
                this.$emit('return', 'checklist', data);
            }
        },
        methods: {
            onCreatedItem(){
                let tempItem = {
                    text: this.contentChecklist.text
                }

                this.items.push(tempItem);
                // this.items.splice(0, 0, tempItem);
                this.$refs.editable.$el.innerHTML = '';
            }
        }
    }
</script>
<style lang="stylus" src="@/assets/styles/tools-created/CheckList.styl"></style>


