<template lang="pug">
    .wrapper-list
        .header-list
            we-editable(
                class="editable-title",
                ref="title",
                :content="titleBlock",
                @update="titleBlock = $event", 
                placeholder="Titulo da lista"
            )

        ul.list(v-if="items")
            li.item(v-for="item in items")
                span.text {{item.text}}
        
        we-editable(
            class="editable-item",
            ref="editable",
            @keyup.enter.native="onCreatedItem",
            :content="contentList.text",
            @update="contentList.text = $event", 
            placeholder="Digitar novo item"
        )

</template>

<script>
    import Vue from 'vue'
    import WeEditable from '@/components/common/We-Editable.vue'
    
    export default {
        name: 'we-list',
        components:{
            'we-editable': WeEditable
        },
        data () {
            return {
                items: [],
                titleBlock: null,
                contentList: {
                    text: ''
                }
            }
        },
        created(){
            
        },
        mounted(){
            this.$refs.editable.$el.focus();
        },
        watch: {
            items(data){
                this.$emit('return', 'list', data);
            }
        },
        methods: {
            onCreatedItem(){
                let tempItem = {
                    text: this.contentList.text
                }

                this.items.push(tempItem);
                // this.items.splice(0, 0, tempItem);
                this.$refs.editable.$el.innerHTML = '';
            }
        }
    }
</script>
<style lang="stylus" src="@/assets/styles/tools-created/List.styl"></style>


