<template lang="pug">
    .editable(contentEditable='true' @input="update", @keypress='limit($event)')
</template>

<script>
    export default {
        name: 'w-editable',
        props:{
            content: '',
            maxLenght: {
                type: Number,
                default: 9999
            }
        },
        data () {
            return {
                msg: 'Welcome to Your Vue.js App'
            }
        },
        mounted:function(){
            this.$el.innerText = this.content;
        },
        methods:{
            update:function(event){
                this.$emit('update',event.target.innerText);
            },
            limit(ev){
                if (ev.target.innerText.length >= this.maxLenght) {
                    ev.preventDefault();
                }
            }
        }
    }
</script>
<style lang="stylus" scope src="@/assets/styles/common/Editable.styl"></style>


