<template lang="pug">
	.task-card(@click="openTask(data.id, idCol, $event.target)")
		.content-card(:class="isOthersUsers ? 'isOthers' : ''")
			.users
				we-userThumb(v-for="(user, index) in data.users", :data="user", :class="user.master ? 'master' : ''") {{user}}
				//- .wrapper-count-users(v-if="data.users.length >= 2")
				//- 	button.button-count 2
				//- 	.list-users
				//- 		.user-name(v-for="(user, index) in data.users", v-if="index >= 2") {{user.infos.name}}

			.content-text
				h3.title {{data.content.title}}

				.footer-content
					.date {{data.content.date}}
					.count-list {{data.content.list}}
					.count-comments {{data.content.comments}}
					.count-anexos {{data.content.anexos}}
</template>

<script>
	import WeUserThumb from '@/components/common/We-UserThumb.vue'

	export default {
		name: 'w-colboard',
		components:{
			'we-userThumb': WeUserThumb,
		},
		props: {
			data: Object,
			idCol: Number,
			idBoard: Number
		},
		data () {
			return {
				isOthersUsers: false
			}
		},
		mounted(){
			if (this.data.users.length > 1) {
				this.isOthersUsers = true
			}else{
				this.isOthersUsers = false
			}
		},
		methods:{
			stop(){},
			openTask(taskId, colId, item){
				let checkDrop = item.getAttribute('data-drag');
				this.$router.push({
				    name: 'task',
				    params: {
				        type: 'task',
				        idBoard: this.idBoard,
				        idTask: taskId,
				        idCol: colId
				    }
				})
			}
		}
	}
</script>
<style lang="stylus" scope src="@/assets/styles/boards/Task.styl"></style>


